import {Point} from './point';

class Bezier {
    constructor(public p1: Point, public c1: Point, public c2: Point, public p2: Point) {}
    
    length() {
        let 
            steps = 10,
            len = 0,
            t, cx, cy, px, py, dx, dy;

        for (let i = 0; i <= steps; i++) {
            t = i / steps;
            cx = Bezier.PointAt(t, this.p1.x, this.c1.x, this.c2.x, this.p2.x);
            cy = Bezier.PointAt(t, this.p1.y, this.c1.y, this.c2.y, this.p2.y);

            if (i > 0) {
                dx = cx - px;
                dy = cy - py;
                len += Math.sqrt(dx * dx + dy * dy);
            }

            px = cx;
            py = cy;
        }
        return len;
    }
    
    static  PointAt(t: number, start: number, c1: number, c2: number, end: number) {
        let rT = 1.0 - t;
        let t2 = t * t; 

        return ( start * rT * rT * rT ) + ( 3.0 * c1 * rT * rT * t ) + ( 3.0 * c2 * rT * t2 ) + end * t2 * t;
    }

    static CalculateControlPoints(s1: Point, s2: Point, s3: Point) {
        var dx1 = s1.x - s2.x, dy1 = s1.y - s2.y,
            dx2 = s2.x - s3.x, dy2 = s2.y - s3.y,

            m1 = {x: (s1.x + s2.x) / 2.0, y: (s1.y + s2.y) / 2.0},
            m2 = {x: (s2.x + s3.x) / 2.0, y: (s2.y + s3.y) / 2.0},

            l1 = Math.sqrt(dx1*dx1 + dy1*dy1),
            l2 = Math.sqrt(dx2*dx2 + dy2*dy2),

            dxm = (m1.x - m2.x),
            dym = (m1.y - m2.y),

            k = l2 / (l1 + l2) || 0,
            cm = {x: m2.x + dxm*k, y: m2.y + dym*k},

            tx = s2.x - cm.x,
            ty = s2.y - cm.y;

        return {
            c1: new Point(m1.x + tx, m1.y + ty),
            c2: new Point(m2.x + tx, m2.y + ty)
        };
    };
}



export {
    Bezier
};


